import React, { useContext, useState } from 'react'
import { serverReq } from '../../functions'
import { UserContext } from '../Entrance'
export default function Login() {
    
    const [user, setUser] = useContext(UserContext)
    const [error, setError] = useState('')

    async function login(e) {
        try {
            
            e.preventDefault()
            const values = Object.values(e.target)
            .reduce((acc, input) => !input.name ? acc : ({
                ...acc,
                [input.name]: input.type == 'checkbox' ? input.checked : input.value
            }), {}
            )
           
            const res = await serverReq('post', '/login', {"email": values.email, "password": values.password})
            // await axios.post('http://localhost:4000/login', {"email": values.email, "password": values.password})
            sessionStorage.token = res.token
            if (values.stayConnected) localStorage.token = res.token
            setUser(res)
            
        } catch (error) {
            setError(error.response?.data?.error || error.message)
        }

    }
    return (
        <div>
            <form id="login" className="Login" onSubmit={(e) => login(e)} >
                <div>{error}</div>

                <label htmlFor="email">אימייל</label>
                <input id="email" type="text" name="email" placeholder="אימייל" required minLength="8" maxLength="40"/>

                <label htmlFor="password">סיסמא</label>
                <input id="password" type="password" name="password" placeholder="סיסמא" required />

                <div>
                    <input id="stayConnected" type="checkbox" name="stayConnected" />
                    <label htmlFor="stayConnected">השאר מחובר</label>
                </div>

                <input type="submit" value="התחבר" />
            </form>
        </div>
    )
}
