
import React, { createContext, useEffect, useState } from 'react'
import './EntranceStyle.css'
import Login from './Login/Login'
import Register from './Register/Register'
import Loding from '../Components/Loding/Loding'
import { serverReq } from '../functions'

export const UserContext = createContext()

export default function Entrance({ children }) {
    const [user, setUser] = useState('gust')
    const [registerState, setRegisterState] = useState(false)

    useEffect(() => {
        async function loginWithToken() {
            try {
                if (localStorage.token || sessionStorage.token) {
                    const response =   await serverReq('post', '/locaSLogin')
                    console.log(`In Entrance page, responce: ${response}`)
                    setUser(response)
                }
                else{
                    setUser()
                }
                
            } catch (error) {
                console.log(`In Entrance page, error: ${error.response?.data?.error || erroe.message || error}`)
                
            }
        }
        loginWithToken()
      }, [])





    function toRegister(state) {
        setRegisterState(state)
    }

    return (
        <UserContext.Provider value={[user, setUser]}>
            {user ? user === 'gust' ? <Loding /> : children :
                <div className="LoginRegisterContainer">
                    <div>
                        <button onClick={() => toRegister(false)}> היכנס</button>
                        <button onClick={() => toRegister(true)}> הירשם</button>
                    </div>
                    {registerState ? <Register toConnect={[registerState, setRegisterState]} /> : <Login />}
                </div>
            }   
        </UserContext.Provider>
    )
}
