import React, { useContext } from 'react'
import { Link, useHistory } from 'react-router-dom'
import { UserContext } from '../../Entrance/Entrance'
import './UserHeaderStyle.css'

export default function UserHeader() {
    const user = useContext(UserContext)
    const hustory = useHistory()
    function logout() {
        localStorage.removeItem("token");
        sessionStorage.removeItem('token');
        history.go(0)
    }

    return (
        <div>
            <div className="userContainer">
                    <span className="logout" onClick={logout}>התנתק</span>
                    <div className="HeaderImg" style={{ backgroundImage: `url(${user?.profilePicture || 'https://hook.finance/sites/default/files/user.png'})` }}></div>
                </div>
        </div>
    )
}
