import { useContext } from 'react'
import { Route, Switch } from 'react-router-dom'
import Entrance, { UserContext } from '../Entrance/Entrance'
import Login from '../Entrance/Login/Login'
import Header from '../Header/Header'
import Dashboard from '../Pages/Dashboard/Dashboard'
import MyTests from '../Pages/MyTests/MyTests'
import Test from '../Pages/Test/Test'
import TestForm from '../Pages/TestForm/TestForm'
import './app.css'



function App() {
  return (
    <div className='App'>
      <Entrance>
          <Header />
          <Switch>
            <Route path="/" component={Dashboard} exact /> {/* created test */}
            <Route path="/my-tests" component={MyTests} />{/* assigend test */}
            <Route path="/test/:testId?" component={Test} />{/* created test */}
            <Route path="/test-form/:testId?" component={TestForm} />{/* created test */}
          </Switch>
      </Entrance>
    </div>
  )
}

export default App
