import React from 'react'
import './TestLinkShowStyle.css'

export default function TestLinkShow({test}) {
    return (
        <div className="testContainer">
            <h4>{test.name}</h4>
            <h4>{test.state}</h4>
            <h4>זמן: {test.timeForTest}</h4>
            <h4>מי הבוחן - {test.creator_name}</h4>
        </div>
    )
}
