import React, { useContext } from 'react'
import { Link, NavLink } from 'react-router-dom'
import { UserContext } from '../Entrance/Entrance'
import './HeaderStyle.css'
import UserHeader from './UserHeader/UserHeader'

export default function Header() {
    return (
        <div className="headerContainer">
            <header className="header">
              <Link to="/" ><div className="logoContainer"></div></Link>
                <div className="linksContainer">
                    <NavLink activeClassName="Linkactive" className="Link" to="/" exact={true}>מבחנים שיצרתי</NavLink>
                    <NavLink activeClassName="Linkactive" className="Link" to="/my-tests" >מבחנים</NavLink>
                    <NavLink activeClassName="Linkactive" className="Link" to='/about' >מי אנחנו</NavLink>
                </div>
                <UserHeader />
            </header>
        </div>
    )
}
