import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { serverReq } from '../../functions'
import './MyTestsStyle.css'
import TestLinkShow from './TestLinkShow/TestLinkShow'

export default function MyTests() {
    const [testsState, setTestesState] = useState([])

  useEffect(() => {
    getTests()
  }, [])
  
  async function getTests() {
    try {
      const res = await serverReq('post', '/my_tests')
      //await axios.post('http://localhost:4000/my_tests', {}, {headers: {authorization: token}})
      const tests = res
      console.log(tests);
      setTestesState(tests)
    } catch (error) {
      console.log(error.response?.data?.error || error.message || error);
    }
  }
    return (
        <div className="TestsContainer">
            {
                testsState.map( test => {
                    return <Link className="Link" to={`/test/${test._id}`} key={test._id}><TestLinkShow test={test} key={test._id}/></Link> 
                })
            }
        </div>

    )
}
