import React, { useEffect, useState } from 'react'
import { getToken, serverReq } from '../../../functions'
import './TestsTableStyle.css'

export default function TestsTable() {
  const [tableState, setTableState] = useState([])

  useEffect(() => {
    getTestes()
  }, [])
  
  async function getTestes() {
    try {
      const res = await serverReq('post', '/my_created')
      // await axios.post('http://localhost:4000/my_created', {}, {headers: {authorization: token}})
      const tests = res
      console.log(tests);
      setTableState(tests)
    } catch (error) {
      console.log(error.response?.data?.error || error.message || error);
    }
  }
  return (
    <div>
        <table>
        <tbody>
        <tr>
          <th>שם מבחן</th>
          <th>סטטוס</th>
          <th>סוג</th>
          <th>תאריך יצירה</th>
        </tr>
        {tableState.map(({name, status, created, typeForm, _id}) => {
          return (
            <tr key={_id}>
              <td key={_id + 'name'}>{name}</td>
              <td key={_id + 'status'}>{status}</td>
              <td key={_id + 'typeForm'}>{typeForm}</td>
              <td key={_id + 'created'}>{created}</td>
            </tr>
          )
        })}
       
       </tbody>
      </table>
    </div>
  )
}
